	module.exports = {"data":{"wordpressPost":{"title":"Why Victoria&#8217;s Secret &#8216;What Is Sexy&#8217; List Is Wrong","content":"<p>Victoria&#8217;s Secret, yes, the lingerie line, just published it&#8217;s 2017 #WhatIsSexy list and it is kind of offensive. Why? Because the winners are pretty much all the following: WHITE. BLONDE. THIN.</p>\n<p>That&#8217;s cool. We don&#8217;t mean to say that women (and men) who fall into all of those categories can&#8217;t be sexy. Of course they can. But excuse us &#8211; folk who DON&#8217;T tick all of those boxes can be sexy too, only for some reason they don&#8217;t seem to have made the cut.</p>\n<p>The list, which includes categories such as Sexiest Actress and Sexiest Sense of Humour, was created by the underwear brand, which then supposedly put it to the public to suggest which celebrity should win the category. This was achieved by the deeply scientific process (Ahem &#8211; not) of using the hash tag #whatissexy on social media.</p>\n<figure id=\"attachment_1771\" style=\"width: 242px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-1771\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/20203100/Ash-JPG-242x300.jpg\" alt=\"\" width=\"242\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/20203100/Ash-JPG-242x300.jpg 242w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/20203100/Ash-JPG.jpg 477w\" sizes=\"(max-width: 242px) 100vw, 242px\" /><figcaption class=\"wp-caption-text\"><strong><em>The sexy full figure of Ashley Graham. Image: Instagram/@theashleygraham</em></strong></figcaption></figure>\n<p>&nbsp;</p>\n<p>Who knows how valid that process was, but like we say the &#8216;winners&#8217; are predominantly twig-sized, Western, blonde babes. Where are the African Americans? The Chinese? The South Americans? The list goes on.</p>\n<p>Yes, there&#8217;s a nod or two towards ethnicity with women such as Vanessa Hudgens (she&#8217;s half Filipino) taking out a category and Chrissy Teigen too (she&#8217;s half Thai). But these are women who while of multi-cultural makeup, still look Western and also, fulfil the slim-hipped, thin-thighed babe Western culture ideal.</p>\n<figure id=\"attachment_1772\" style=\"width: 240px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-1772\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/20203233/VS-JPG-240x300.jpg\" alt=\"\" width=\"240\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/20203233/VS-JPG-240x300.jpg 240w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/20203233/VS-JPG.jpg 476w\" sizes=\"(max-width: 240px) 100vw, 240px\" /><figcaption class=\"wp-caption-text\"><strong><em>The classic Victoria&#8217;s Secret model look. Sexy? Heck yes. But sexy comes in others shapes, sizes and colours too. Image: Instagram/@victoriassecret</em></strong></figcaption></figure>\n<p>&nbsp;</p>\n<p>Where are the curvy girls and women? Why no Ashley Graham? Or Iskra Lawrence? Why no black skin? What this brand has done has created a list that perpetuates the stereotypes we would so dearly like to see disappear. And it seems a shame if nothing else.</p>\n<p>&nbsp;</p>\n<p>&nbsp;</p>\n<p>&nbsp;</p>\n<p>&nbsp;</p>\n<p>&nbsp;</p>\n<p>&nbsp;</p>\n<p>&nbsp;</p>\n<p>&nbsp;</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/20203003/theashleygraham.jpg"},"categories":[{"slug":"beauty-talk","name":"Beauty Talk"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"7f7a1d34-9977-5609-aadf-a31ed0ee46fe"}}

